:root {
    --header-height: 90px;
    --footer-height: 90px;
    --accent-colour: #cc9c54;
    --accent-colour: rgb(200, 160, 140);
}



/* ==================== REMOVE BROWSER STYLING ==================== */
* {
    margin: 0;
    padding: 0;
    outline: 0;
    border: 0;
    text-decoration: none;
    list-style: none;
    box-sizing: border-box;
}



/* ==================== DEFAULT STYLING ==================== */
body {
    font-size: 16px;
    font-weight: 300;
    font-family: 'Roboto', sans-serif;
    background-image: url(${___CSS_LOADER_URL_REPLACEMENT_0___});
    background-size: cover;
    color: white;
}

button {
    cursor: pointer;
}



/* ==================== HTML TEMPLATE ==================== */
#grid-container {
    height: 100vh;
    width: 100%;
    display: grid;
    grid-template-rows: var(--header-height) 1fr var(--footer-height);
    grid-template-areas:
        "header"
        "content"
        "footer";
}

    header {
        grid-area: header;
        display: flex;
        align-items: center;
        justify-content: center;
        padding: 0 36px 0;
        opacity: 0.8;
        backdrop-filter: blur(10px);
        background-color: black;
    }

        nav {
            width: 100%;
            display: flex;
            align-items: center;
            justify-content: space-between;
        }

            nav > * {
                display: flex;
                color: white;
                font-weight: 400;
            }

            .nav-left, .nav-right {
                width: 240px;
            }

            .nav-left {
                justify-content: start;
            }

            .nav-right {
                justify-content: end;
            }

            .nav-center {
                justify-content: center;
                gap: 3rem;
            }
            
                .nav-btn {
                    height: 45px;
                    font-size: 16px;
                    font-weight: 400;
                    color: white;
                    background-color: transparent;
                }

                    .nav-btn.active {
                        border-bottom: 2px solid white;
                    }

                    .nav-btn:hover {
                        border-bottom: 2px solid var(--accent-colour);
                    }

                .reserve-btn {
                    height: 45px;
                    width: 200px;
                    font-size: 16px;
                    font-weight: 400;
                    color: white;
                    background-color: transparent;
                    border: 1px solid var(--accent-colour);
                }

    footer {
        grid-area: footer;
        display: flex;
        align-items: center;
        justify-content: center;
        gap: 0.5rem;
    }

        footer p {
            font-size: 16px;
            cursor: default;
            user-select: none;
        }

        footer a {
            text-decoration: none;
            color: inherit;
        }

            .fa-github {
                font-size: 22px;
                transition: transform 0.3s ease-in-out;
            }
        
            .fa-github:hover {
                transform: rotate(360deg) scale(1.2);
            }



/* ==================== CONTENT CONTAINER ==================== */
#content {
    grid-area: content;
    height: calc(100vh - var(--header-height) - var(--footer-height));
    overflow-y: hidden;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}

#content > *:not(.menu-nav):not(.menu-scroll):not(.contact) {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}



/* ==================== HOME ==================== */
.home {
    height: 100%;
    width: 100%;
    gap: 1rem;
}

    .home-title {
        
    }

        .home-title > h1 {
            color: var(--accent-colour);
            font-size: 72px;
            text-shadow: 1px 5px 15px rgba(0, 0, 0, 0.8);
        }

        .home-title > h2 {
            margin-top: -30px;
            margin-left: 50px;
            color: white;
            font-size: 48px;
        }

    hr {
        margin-bottom: 10px;
        width: 250px;
        border: 1px solid var(--accent-colour);
    }

    .home-buttons {
        display: flex;
        flex-direction: row;
        gap: 2rem;
    }

        .home-buttons > button {
            padding: 12px 24px 12px 24px;
            border-radius: 24px;
            color: inherit;
            font-size: inherit;
            font-weight: 700;
            background-color: var(--accent-colour);
            text-shadow: 1px 1px 5px rgba(0, 0, 0, 0.5);
            box-shadow: 1px 5px 15px rgba(0, 0, 0, 0.5);
        }

        .home-buttons > button:hover {
            transform: scale(1.02);
        }



/* ==================== MENU ==================== */
.menu-nav {
    grid-area: nav;
    width: 650px;
    padding: 0 72px 0;
    display: flex;
    justify-content: space-evenly;
}

    .menu-nav > button {
        height: 45px;
        font-size: 16px;
        font-weight: 400;
        color: white;
        background-color: transparent;
    }

    .menu-nav > button:hover {
        color: var(--accent-colour);
    }

.menu-scroll {
    height: 750px;
    width: 650px;
    box-shadow: 1px 5px 15px rgba(0, 0, 0);
    overflow-y: auto;
}

.menu-scroll::-webkit-scrollbar {
    width: 10px;
}

.menu-scroll::-webkit-scrollbar-thumb {
    background-color: white;
}

    .menu {
        padding: 25px 0;
        opacity: 0.8;
        backdrop-filter: blur(5px);
        background: linear-gradient(
            to right,
            white 0%,
            white 30%,
            var(--accent-colour) 30%,
            var(--accent-colour) 100%
        );
        display: grid;
        grid-auto-rows: 1fr;
        gap: 2rem;
    }

        .menu-item {
            display: grid;
            grid-template-columns: 30% 1fr 10%;
            grid-template-rows: max-content 1fr;
            grid-template-areas:
                "image name price"
                "image description description";
        }

            .item-image {
                grid-area: image;
                justify-self: center;
                height: 120px;
                width: 120px;
                border-radius: 12px;
                background-color: rgb(190, 190, 190);
            }

            .item-name {
                grid-area: name;
                padding-left: 24px;
                font-weight: 700;
            }

            .item-price {
                grid-area: price;
                font-weight: 700;             
            }

            .item-description {
                grid-area: description;
                padding-top: 12px;
                padding-left: 24px;
            }



/* ==================== ABOUT ==================== */
.about {
    
}

    .about-title > h1 {
        color: var(--accent-colour);
        font-size: 72px;
        text-shadow: 1px 5px 15px rgba(0, 0, 0, 0.8);
    }

    .about-title > h2 {
        margin-top: -30px;
        margin-left: 50px;
        color: white;
        font-size: 48px;
    }

    .about > p {
        width: 60%;
        margin-top: 24px;
        font-size: 18px;
        font-weight: 400;
        text-align: center;
        line-height: 1.5;
        text-shadow: 1px 5px 5px rgba(0, 0, 0, 0.8);
    }



/* ==================== CONTACT ==================== */ 
.contact {
    height: 550px;
    width: 650px;
    display: grid;
    grid-template-columns: 40% 60%;
    grid-template-areas:
        "info message";
    gap: 2rem;
}

    .contact > * {
        padding: 24px;
        opacity: 0.8;
        box-shadow: 1px 5px 15px rgba(0, 0, 0);
        backdrop-filter: blur(10px);  
    }

    .info {
        grid-area: info;
        background-color: white;
        color: black;
        position: relative;
    }

        .info > :nth-child(2),
        .info > :nth-child(3) {
            margin-top: 3rem;
        }

            .info > *:not(:nth-child(4)) > *:not(:first-child) {
                margin-left: 0.5rem;
            }

            .info > *:not(:nth-child(4)) > :nth-child(2) {
                margin-top: 0.5rem;
            }

        .socials {
            position: absolute;
            bottom: 32px;
            left: 130px;
            transform: translate(-50%, 0);
            height: 42px;
            font-size: 24px;
            gap: 1rem;
            display: flex;
            justify-content: center;
            align-items: center;
        }

            .socials > *:hover {
                cursor: pointer;
            }

    .message {
        grid-area: message;
        background-color: var(--accent-colour);
        position: relative;
        display: grid;
        grid-template-rows: max-content 1fr;
    }
        form {
            margin-top: 1rem;
            display: flex;
            flex-direction: column;
            gap: 1rem;
        }

            .form-input {
                display: flex;
                flex-direction: column;
                gap: 0.5rem;
            }

                .form-input > label {
                    font-weight: 700;
                }

                .form-input > input {
                    border-radius: 10px;
                    padding: 10px;
                    background-color: white;
                    font-size: 14px;
                }

                #message {
                    height: 180px;
                    border-radius: 10px;
                    padding: 10px;
                    background-color: white;
                    font-size: 14px;
                    font-family: inherit;
                }
            
            form > button {
                position: absolute;
                bottom: 32px;
                left: 195px;
                transform: translate(-50%, 0);
                border-radius: 24px;
                padding: 12px 24px 12px 24px;
                font-size: inherit;
                font-weight: 700;
                color: black;
                background-color: white;
                box-shadow: 1px 5px 15px rgba(0, 0, 0, 0.5);
            }
