\n        .menu-item {\n            display: grid;\n            grid-template-columns: 30% 1fr 10%;\n            grid-template-rows: max-content 1fr;\n            grid-template-areas:\n                \"image name price\"\n                \"image description description\";\n        }\n\n            .item-image {\n                grid-area: image;\n                justify-self: center;\n                height: 120px;\n                width: 120px;\n                border-radius: 12px;\n                background-color: rgb(190, 190, 190);\n            }\n\n            .item-name {\n                grid-area: name;\n                padding-left: 24px;\n                font-weight: 700;\n            }\n\n            .item-price {\n                grid-area: price;\n                font-weight: 700;             \n            }\n\n            .item-description {\n                grid-area: description;\n                padding-top: 12px;\n                padding-left: 24px;\n            }\n\n\n\n/* ==================== ABOUT ==================== */\n.about {\n    \n}\n\n    .about-title > h1 {\n        color: var(--accent-colour);\n        font-size: 72px;\n        text-shadow: 1px 5px 15px rgba(0, 0, 0, 0.8);\n    }\n\n    .about-title > h2 {\n        margin-top: -30px;\n        margin-left: 50px;\n        color: white;\n        font-size: 48px;\n    }\n\n    .about > p {\n        width: 60%;\n        margin-top: 24px;\n        font-size: 18px;\n        font-weight: 400;\n        text-align: center;\n        line-height: 1.5;\n        text-shadow: 1px 5px 5px rgba(0, 0, 0, 0.8);\n    }\n\n\n\n/* ==================== CONTACT ==================== */ \n.contact {\n    height: 550px;\n    width: 650px;\n    display: grid;\n    grid-template-columns: 40% 60%;\n    grid-template-areas:\n        \"info message\";\n    gap: 2rem;\n}\n\n    .contact > * {\n        padding: 24px;\n        opacity: 0.8;\n        box-shadow: 1px 5px 15px rgba(0, 0, 0);\n        backdrop-filter: blur(10px);  \n    }\n\n    .info {\n        grid-area: info;\n        background-color: white;\n        color: black;\n        position: relative;\n    }\n\n        .info > :nth-child(2),\n        .info > :nth-child(3) {\n            margin-top: 3rem;\n        }\n\n            .info > *:not(:nth-child(4)) > *:not(:first-child) {\n                margin-left: 0.5rem;\n            }\n\n            .info > *:not(:nth-child(4)) > :nth-child(2) {\n                margin-top: 0.5rem;\n            }\n\n        .socials {\n            position: absolute;\n            bottom: 32px;\n            left: 130px;\n            transform: translate(-50%, 0);\n            height: 42px;\n            font-size: 24px;\n            gap: 1rem;\n            display: flex;\n            justify-content: center;\n            align-items: center;\n        }\n\n            .socials > *:hover {\n                cursor: pointer;\n            }\n\n    .message {\n        grid-area: message;\n        background-color: var(--accent-colour);\n        position: relative;\n        display: grid;\n        grid-template-rows: max-content 1fr;\n    }\n        form {\n            margin-top: 1rem;\n            display: flex;\n            flex-direction: column;\n            gap: 1rem;\n        }\n\n            .form-input {\n                display: flex;\n                flex-direction: column;\n                gap: 0.5rem;\n            }\n\n                .form-input > label {\n                    font-weight: 700;\n                }\n\n                .form-input > input {\n                    border-radius: 10px;\n                    padding: 10px;\n                    background-color: white;\n                    font-size: 14px;\n                }\n\n                #message {\n                    height: 180px;\n                    border-radius: 10px;\n                    padding: 10px;\n                    background-color: white;\n                    font-size: 14px;\n                    font-family: inherit;\n                }\n            \n            form > button {\n                position: absolute;\n                bottom: 32px;\n                left: 195px;\n                transform: translate(-50%, 0);\n                border-radius: 24px;\n                padding: 12px 24px 12px 24px;\n                font-size: inherit;\n                font-weight: 700;\n                color: black;\n                background-color: white;\n                box-shadow: 1px 5px 15px rgba(0, 0, 0, 0.5);\n            }\n"],"sourceRoot":""}]);
